.contaner {
  max-width: 1320px;
}

//
.w {
  @include media-breakpoint-up(xl) {
    &-xl {
      &-20 {
        width: 20%;
      }
      &-25 {
        width: 25%;
      }
      &-55 {
        width: 55%;
      }
    }
  }
}

//google: azeret-mono
.ff-azeret-mono {
  font-family: 'Azeret Mono', monospace;
}

//
.gradient {
  //: ->
  &-x {
    @include gradient-x;
  }
  //: ->
  &-y {
    @include gradient-y;
  }
}
//: ->
.text-gradient-y{
  @include text-gradient-y;
}
// 
.btn-gradient {
  &-x {
    @include gradient-x;
    &:hover {
      opacity: 0.75;
    }
  }
  &-y {
    @include gradient-y;
    &:hover {
      opacity: 0.75;
    }
  }
}

//-
.fs {
  @include font-size;
  @include media-breakpoint-up(sm) {
    &-sm {
      @include font-size;
    }
  }
  @include media-breakpoint-up(lg) {
    &-lg {
      @include font-size;
    }
  }
  @include media-breakpoint-up(xl) {
    &-xl {
      @include font-size;
    }
  }
}

//Border-radius
.rounded-4 {
  border-radius: 0.5rem; // 8px
}

.rounded-5 {
  border-radius: 0.75rem; // 12px
}

.rounded-6 {
  border-radius: 1.5rem; // 24px
}

/* hover */
//hover: 
.hover-text-primary {
  &:hover {
    color: $primary !important; 
  }
}
//hover: border
.hover-border-primary {
  &-2 {
    &:hover {
      margin: -1px;
      border: 2px solid $primary;
    }
  }
}
//hover: border + 
.hover-link-text-primary {
  &:hover {
    color: $primary !important; 
    border: 1px solid $primary !important;
  }
}

//hover: border +  (->)
.hover-link-text-gradient-y {
  &:hover {
    @include text-gradient-x;
    border: 1px solid $primary !important;
  }
}

//hover: 
.hover-link-opacity {
  &:hover {
    opacity: 0.7;
  }
}

//-
.pseudo-square-primary {
  display: flex;
  align-items: center;
  &::before {
    content: '';
    width: 5px;
    height: 5px;
    background-color: $primary;
    margin-right: 8px;
  }
}
